body {
  margin: 0;
  font-family: "Montserrat",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.blueText {
  color: #00b5b7;
  text-align: center;
  margin: 0px
}

.inputHeader{
  color: #111;
  float: left;
  margin: 0px;
}

.input{
  border: 0px;
  border-bottom: 2px solid #00b5b7;
  margin: 0px;
  float: right;
  font-size: large;
  font-family: "Montserrat";
}

form {
  padding: 10px 20px 0px 20px;
  width: 45%;
  margin: auto;
}

.buttonDiv{
  text-align: center;
}

button{
  background-color: white;
  border: 2px solid #00b5b7;
  width: 35%;
  font-size: large;
  font-family: "Montserrat";
  cursor: pointer;
  border-radius: 5px;
}

.chart{
  margin: auto;
  width: 50%;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}